import React from 'react'
import generateCode from "./generateCode";

export default class Start extends React.Component {
    render() {
        return (
            <div className='contentWrapper color-purple backcolor-purple'>
                <div className='ym-content'>
                    <div className="title">
                        <h1>Let's Start</h1>
                    </div>
                    <div className="main">
                        <h2>YMUI</h2>
                        <div className="intro-text">
                            <a href="https://github.com/yingmhd/ymplugins/tree/master/ymplugin" target="_blank" className="color-green"></a>
                            <code>ymui.css</code><code>import</code><code>ymui.js</code>import
                        </div>
                        <div className="show-area">
                            {generateCode(`
// css
<link rel="stylesheet" type="text/css" href="ymui.css">
// js
<script src="ymui.js" type="text/javascript"></script>
// import
import YMPlugins from "ymui.js"
                            `)}
                        </div>
                        <h2> Viewport Meta </h2>
                        <div className="intro-text">
                            Viewport<code>meta</code><code>{'<head>'}</code>
                        </div>
                        <div className="show-area">
                            <pre>
                                <p>{'<meta name="viewport" content="width=device-width, initial-scale=1">'}</p>
                            </pre>
                        </div>
                        <h2>VUE</h2>
                        <div className="intro-text">
                            reactvue<a href="http://blog.ymhd.xyz/posts/2019/09/05/vue%E5%8F%8C%E5%90%91%E6%95%B0%E6%8D%AE%E7%BB%91%E5%AE%9A%E5%8E%9F%E7%90%86.html" target="_blank" className="color-green"></a>
                             <a href="https://github.com/yingmhd/ymplugins/tree/master/ymvue" target="_blank" className="color-green"></a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


