import React from 'react'
import YMPlugins from '../ymui'

export default class Page extends React.Component {
    componentDidMount() {
        new YMPlugins.tip();
        new YMPlugins.tip({
            element: '#openTip',
            position: 'up',
            content: ''
        });
    }

    render() {
        return (
            <div className='contentWrapper color-blue backcolor-blue'>
                <div className='ym-content'>
                    <div className="title">
                        <h1>Tip</h1>
                    </div>
                    <div className="main">
                        <h2></h2>
                        <div className="intro-text">
                            class<code>ym-tip</code><code>data-tip</code><code>data-position</code>js
                        </div>
                        <div className="show-area">
                            <div className="element">
                                <button className="ym-btn ym-btn-blue ym-tip" data-tip="tip on left" data-position="left"></button>
                                <button className="ym-btn ym-btn-blue ym-tip" data-tip="tip on up" data-position="up"></button>
                                <button className="ym-btn ym-btn-blue ym-tip" data-tip="tip on bottom" data-position="bottom"></button>
                                <button className="ym-btn ym-btn-blue ym-tip" data-tip="tip on right" data-position="right"></button>
                            </div>
                            <pre>
                                <p>{'<button class="ym-btn ym-btn-blue ym-tip" data-tip="tip on left" data-position="left"></button>'}</p>
                                <p>{'<button class="ym-btn ym-btn-blue ym-tip" data-tip="tip on up" data-position="up"></button>'}</p>
                                <p>{'<button class="ym-btn ym-btn-blue ym-tip" data-tip="tip on bottom" data-position="bottom"></button>'}</p>
                                <p>{'<button class="ym-btn ym-btn-blue ym-tip" data-tip="tip on right" data-position="right"></button>'}</p>
                                <p className="color-gray">{'// js'}</p>
                                <p>{'new YMPlugins.tip();'}</p>
                            </pre>
                        </div>
                        <h2></h2>
                        <div className="intro-text">
                            
                        </div>
                        <div className="show-area">
                            <div className="element">
                                <button className="ym-btn ym-btn-blue" id="openTip"></button>
                            </div>
                            <pre>
                                <p>{'<button class="ym-btn ym-btn-blue" id="openTip"></button>'}</p>
                                <p className="color-gray">{'// js'}</p>
                                <p>{'new YMPlugins.tip({'}</p>
                                <p>{'   element: \'#openTip\','}<span className="color-gray"> //idclass</span></p>
                                <p>{'   position: \'up\','}</p>
                                <p>{'   content: \'\''}</p>
                                <p>{'});'}</p>
                            </pre>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


