import React from 'react'
import YMPlugins from '../ymui'

export default class Page extends React.Component {
    componentDidMount() {
        new YMPlugins.carousel({
            containerId: 'ymCarousel',
            width: '600',
            height: '400',
            hoverHideItems: false,
            hoverStopMove: true,
            hasBtn: true
        })
    }

    render() {
        return (
            <div className='contentWrapper color-blue backcolor-blue'>
                <div className='ym-content'>
                    <div className="title">
                        <h1>Carousel</h1>
                    </div>
                    <div className="main">
                        <h2></h2>
                        <div className="intro-text">
                            DOM<code>id</code><code>li</code>,js
                        </div>
                        <div className="show-area">
                            <div className="element">
                                <div className="ym-carousel-container" id="ymCarousel">
                                    <ul className="ym-carousel">
                                        <li>Carousel One</li>
                                        <li>Carousel Two</li>
                                        <li>Carousel Three</li>
                                        <li>Carousel Four</li>
                                    </ul>
                                </div>
                            </div>
                            <pre>
                                <p className="color-gray">{'// '}</p>
                                <p>{'<div class="ym-carousel-container" id="ymCarousel">'}</p>
                                <p>{'   <ul class="ym-carousel">'}</p>
                                <p>{'       <li>Carousel One</li>'}</p>
                                <p>{'       <li>Carousel Two</li>'}</p>
                                <p>{'       <li>Carousel Three</li>'}</p>
                                <p>{'       <li>Carousel Four</li>'}</p>
                                <p>{'   </ul>'}</p>
                                <p>{'</div>'}</p>
                                <p className="color-gray">{'// js'}</p>
                                <p>{'new YMPlugins.carousel({'}</p>
                                <p>{'   containerId: \'ymCarousel\','}</p>
                                <p>{'   width: \'600\','}</p>
                                <p>{'   height: \'400\''}</p>
                                <p>{'   hoverHideItems: false,'}<span className="color-gray">   // true</span></p>
                                <p>{'   hoverStopMove: true,'}<span className="color-gray">   // true</span></p>
                                <p>{'   hasBtn: true'}<span className="color-gray">   // false</span></p>
                                <p>{'})'}</p>
                            </pre>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


