import React from 'react'
import YMPlugins from '../ymui'

var showEvent = function (dom) {
    console.log(dom);
};

export default class Dialog extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            curDialog: null
        };
    }


    openDialog() {
        let ymDialog = new YMPlugins.dialog({
            theme: 'green',
            title: '',
            content: '',
            ok_fuc: function (dom) {
                alert(1);
                this.close();
            }
        });
        ymDialog.show();
    }

    bindDialogListener() {
        if (this.state.curDialog === null) {
            let dialog = new YMPlugins.dialog({
                theme: 'cyan',
                title: '',
                content: '',
                ok_fuc: function (dom) {
                    this.close();
                }
            });
            dialog.on('show', showEvent);
            this.setState({
                curDialog: dialog
            })
        } else {
            this.state.curDialog.on('show', showEvent);
        }
    }

    removeDialogListener() {
        if (this.state.curDialog !== null) {
            this.state.curDialog.off('show', showEvent);
        }
    }

    emitDialogListener() {
        if (this.state.curDialog === null) {
            let dialog = new YMPlugins.dialog({
                theme: 'cyan',
                title: '',
                content: '',
                ok_fuc: function (dom) {
                    this.close();
                }
            });
            dialog.show();
        } else {
            this.state.curDialog.show();
            this.setState({
                curDialog: null
            })
        }
    }

    render() {
        return (
            <div className='contentWrapper color-blue backcolor-blue'>
                <div className='ym-content'>
                    <div className="title">
                        <h1>Dialog</h1>
                    </div>
                    <div className="main">
                        <h2></h2>
                        <div className="intro-text">
                            
                        </div>
                        <div className="show-area">
                            <div className="element">
                                <button className="ym-btn ym-btn-green" onClick={this.openDialog}></button>
                            </div>
                            <pre>
                                <p className="color-gray">{'// '}</p>
                                <p>{'let ymDialog = new YMPlugins.dialog({'}</p>
                                <p>{'   title: \'\','}</p>
                                <p>{'   content: \'\','}</p>
                                <p>{'   ok_fuc: function (dom) {'}</p>
                                <p>{'       alert(1);'}</p>
                                <p>{'       this.close();'}</p>
                                <p>{'   }'}</p>
                                <p>{'});'}</p>
                                <p className="color-gray">{'// show()'}</p>
                                <p>{'ymDialog.show();'}</p>
                            </pre>
                        </div>
                        <h2></h2>
                        <div className="intro-text">
                            9
                        </div>
                        <div className="show-area">
                            <pre>
                                <p className="color-gray">{'// '}</p>
                                <p>{'let ymDialog = new YMPlugins.dialog({'}</p>
                                <p>{'   title: '}<span className="color-gray">  </span><span
                                    className="color-red">*</span></p>
                                <p>{'   content: '}<span className="color-gray">  </span><span
                                    className="color-red">*</span></p>
                                <p>{'   cancel: '}<span className="color-gray">  </span></p>
                                <p>{'   ok_text: '}<span className="color-gray">  </span></p>
                                <p>{'   cancel_text: '}<span className="color-gray">  </span></p>
                                <p>{'   ok_fuc: '}<span className="color-gray">  </span><span
                                    className="color-red">*</span></p>
                                <p>{'   cancel_fuc: '}<span className="color-gray">  </span><span
                                    className="color-red"></span></p>
                                <p>{'   theme: '}<span className="color-gray">  </span><span
                                    className="color-red">yellow,red,purple,gray,blue,cyan,orange</span></p>
                                <p>{'   dialogId: '}<span className="color-gray">id  </span><span
                                    className="color-red">id</span></p>
                                <p>{'});'}</p>
                                <p>{'ymDialog.show();'}</p>
                            </pre>
                        </div>
                        <h2></h2>
                        <div className="intro-text">
                            DOM<code>this</code><code>this</code>
                            <code>this.close()</code>
                        </div>
                        <div className="show-area">
                            <pre>
                                <p>{'   ...'}</p>
                                <p>{'   ok_fuc: function (dom) {'}</p>
                                <p>{'       console.log(dom);'}</p>
                                <p>{'       this.close();'}</p>
                                <p>{'   }'}</p>
                                <p>{'   ...'}</p>
                            </pre>
                        </div>
                        <h2></h2>
                        <div className="intro-text">
                            <code>show</code>,<code>close</code>
                        </div>
                        <div className="show-area">
                            <div className="element">
                                <button className="ym-btn ym-btn-green"
                                        onClick={this.bindDialogListener.bind(this)}>
                                </button>
                                <button className="ym-btn ym-btn-red"
                                        onClick={this.removeDialogListener.bind(this)}>
                                </button>
                                <button className="ym-btn ym-btn-cyan"
                                        onClick={this.emitDialogListener.bind(this)}>
                                </button>
                            </div>
                            <pre>
                                <p className="color-gray">{'// '}</p>
                                <p>{'var showEvent = function (dom) {'}</p>
                                <p>{'   console.log(dom);'}</p>
                                <p>{'};'}</p>
                                <p className="color-gray">{'// '}</p>
                                <p>{'let dialog = new YMPlugins.dialog({...});'}</p>
                                <p className="color-gray">{'// show'}</p>
                                <p>{'dialog.on(\'show\',showEvent);'}</p>
                                <p className="color-gray">{'// show'}</p>
                                <p>{'dialog.off(\'show\',showEvent);'}</p>
                                <p>{'// show'}</p>
                                <p>{'dialog.show()'}</p>
                            </pre>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


